/* _sass/_layout.scss */

body {
    margin: 0;
    // margin-top: 0.25em; /* Remove this line or adjust it */
    padding-top: 72px;
    color: var(--text-color); /* Updated to generic variable */
    font-family:
        "Open Sans", sans-serif; /* Replaced with a common font, or replace with your preferred generic font variable if you define one like --body-font */
    font-size: 1.1em;
    line-height: 1.6;
    -webkit-font-smoothing: antialiased;
    transition:
        color 0.3s ease,
        background-color 0.3s ease; // Smooth transition for theme change (added background-color)
    background-color: var(
        --base-color
    ); // Ensure body background also changes with theme
    box-sizing: border-box; /* IMPORTANT: Ensures padding/border are included in total width */
}
h1,
h2,
h3 {
    font-weight: 600;
    color: var(
        --text-color
    ); /* Updated to generic variable (assuming headings share main text color) */
    font-family:
        "Muli", sans-serif; /* Replaced with a common font, or replace with your preferred generic heading font variable */
    line-height: 1.5;
    margin-bottom: 3px;
    transition: color 0.3s ease; // Smooth transition for theme change
}
h1 {
    color: var(
        --sapphire
    ); /* Updated to generic variable, using sapphire as a prominent heading color */
    font-size: 2.5em;
    a,
    a:visited {
        text-decoration: none;
        color: var(--sapphire); /* Updated to generic variable */
        transition: color 0.3s ease; // Smooth transition
    }
}
h2 {
    font-size: 2em;
}
a,
a:visited {
    color: var(--blue); /* Updated to generic variable */
    text-decoration: underline;
    font-weight: bold;
    transition: color 0.3s ease; // Smooth transition for theme change
}
a:hover {
    color: var(--lavender); /* Updated to generic variable (hover state) */
    text-decoration: underline;
}

img {
    max-width: 100%; /* Ensure the image doesn't exceed its container */
    height: auto; /* Maintain the image's aspect ratio */
}

h1 {
    font-size: 2em; /* Adjusted '2m' to '2em' assuming it was a typo */
    margin-top: 10px; /* Adjust the top margin as needed */
}

/* Base styles for main content wrappers */
main,
.container {
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 15px; /* Default side padding for desktop */
    box-sizing: border-box; /* Crucial: Ensures padding is included in the width */
}

/* Blog specific container - if different than main, otherwise main handles it */
.blog-post-content {
    /* Assuming you have a class like this for the actual post content */
    padding: 0 15px;
    max-width: 1000px;
    margin: 0 auto;
    box-sizing: border-box;
}

time {
    color: var(--subtext0-color); /* Updated to generic variable */
    font-size: 0.95em;
    display: block;
    transition: color 0.3s ease; // Smooth transition for theme change
}
.container {
    padding-bottom: 50px; /* Add padding at the bottom of the container */
}
.contact-content {
    margin-bottom: 50px; /* Add bottom margin to create padding at the bottom */
}
.contained-image {
    max-width: 600px;
    width: 100%;
    height: auto;
    display: block;
    margin: 0 auto; /* Optional: Center the image horizontally */
}
.responsive-image {
    max-width: 100%;
    width: 100%;
    max-width: 600px; /* Adjust this to your desired maximum width */
    height: auto;
    display: block;
    margin: 0 auto; /* Optional: Center the image horizontally */
}

.post-navigation {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
}

.prev,
.next {
    width: 30%; /* Adjust as needed */
}

.instagram-media {
    max-width: 25%;
    margin: 0 auto;
}

/* Aside */

aside {
    position: fixed;
    top: 0;
    left: 0;
    background: var(--base-color); /* Updated to generic variable */
    width: 100%;
    z-index: 2;
    border-bottom: 1px solid var(--surface1-color); /* Updated to generic variable */
    box-shadow: 0px 1px 1px var(--overlay0-color-shadow); /* Updated to generic variable */
    transition:
        background 0.3s ease,
        border-color 0.3s ease,
        box-shadow 0.3s ease; // Smooth transition

    nav {
        float: left;
        max-width: 800px; /* This is for your nav, which is separate from main content */
        margin: 0 auto;

        ul {
            margin: 0;
            padding: 0;
            list-style: none;

            li {
                float: left;
                position: relative;

                a {
                    text-decoration: none;
                    display: block;
                    padding: 15px;
                    font-family: "Muli", sans-serif;
                    line-height: 40px;
                    margin-bottom: -1px;
                    box-shadow: 0;
                    color: inherit; // Inherit color from parent by default
                    transition:
                        background 0.3s ease,
                        color 0.3s ease; // Smooth transition

                    &:hover,
                    &:active {
                        background: var(
                            --surface0-color
                        ); /* Updated to generic variable */
                        text-decoration: none;
                        color: var(
                            --text-color
                        ); // Ensure text color is readable on hover
                    }
                }
            }

            /* Add styles for the logo */
            .logo {
                float: left; /* Align the logo to the left */
                margin-right: 20px; /* Adjust the margin as needed */
                margin-top: 0px; /* Add margin to separate the logo from the navigation */

                a {
                    display: block; /* Make the anchor a block-level element to cover the entire logo */

                    /* Remove hover effect on the logo */
                    &:hover {
                        background: none;
                        color: inherit;
                    }
                }

                img {
                    width: 50px; /* Explicitly set width to the desired square size */
                    height: 50px; /* Explicitly set height to the desired square size */
                    object-fit: contain; /* Ensure the image fits within the square without distortion */
                    vertical-align: middle; /* Align the logo vertically in the middle with the text */
                }
            }
            .header-spacer {
                height: 20px; /* Adjust the height for the desired padding */
                width: 100%; /* Make it take the full width */
            }
        }

        &:after {
            content: "";
            display: table;
            clear: both;
        }
    }
}

/* Media queries for overall layout elements */
@media (max-width: 768px) {
    // Adjust main content wrapper for smaller screens
    main,
    .container,
    .blog-post-content {
        /* Target all relevant main content wrappers */
        max-width: 100%; /* Allow them to take full width */
        padding-left: 10px; /* Smaller side padding for mobile */
        padding-right: 10px;
        margin-left: 0; /* Remove auto margins */
        margin-right: 0;
    }

    // Adjust `img` tag specific to `_layout.scss` that might not be carousels
    img:not(.carousel-image):not(.carousel-filmstrip-thumbnail) {
        max-width: 100%; /* Ensure all other images are responsive */
    }

    h1 {
        font-size: 1.8em; /* Adjust for better hierarchy on smaller screens */
    }
    h2 {
        font-size: 1.5em;
    }
    // No specific img max-width: 80% here, rely on 100% and parent padding
}

@media (max-width: 480px) {
    main,
    .container,
    .blog-post-content {
        /* Target all relevant main content wrappers */
        padding-left: 5px; /* Even smaller side padding for very narrow screens */
        padding-right: 5px;
    }

    h1 {
        font-size: 1.5em;
    }
    h2 {
        font-size: 1.2em;
    }
}

